import QRCode from 'qrcode.react';
import React, { useRef } from 'react';
import { useTranslation } from 'react-i18next';
import { Button } from 'shared/components/Elements';

type Props = {
	link: string | null;
};
export function FormsQRCode({ link }: Props) {
	const { t } = useTranslation();
	const qrRef = useRef<HTMLDivElement>(null);

	const downloadQRCode = () => {
		// Convert the QR code to a canvas and then to a data URL
		const canvas = qrRef.current?.querySelector('canvas');

		if (canvas) {
			const image = canvas.toDataURL('image/png').replace('image/png', 'image/octet-stream');
			// Create a link and set the URL as the href
			const link = document.createElement('a');
			link.href = image;
			// Set the download name for the QR code image
			link.download = 'qr-code.png';
			// Append the link to the document and trigger the download
			document.body.appendChild(link);
			link.click();
			document.body.removeChild(link);
		}
	};

	return (
		<div className="feedback-qr__wrapper flex flex-col items-center justify-items-center gap-y-2">
			<div ref={qrRef}>
				<h3 className="mb-2 font-semibold text-kdark-424242 dark:text-kgrey-bgmenu">
					{t('generated_qr_code')}
				</h3>
				<QRCode id="qrCodeEl" value={link ?? ''} size={150} level={'H'} includeMargin={true} />
			</div>
			<Button variant="contained" onClick={downloadQRCode}>
				{t('download_qr_code')}
			</Button>
		</div>
	);
}
