import {
	KntButton,
	Pagination,
	TableBody,
	TableContainer,
	TableHead,
	WhiteModal
} from 'shared/components/Elements';
import EntityContainer from 'modules/entity/containers';
import {
	ConfirmModal,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { IconBtn } from 'shared/components/Chat/components';
import { AiFillCopy, AiOutlineQrcode } from 'react-icons/ai';
import { Trans, useTranslation } from 'react-i18next';
import { useAppState } from 'shared/state';
import { BiPlus } from 'react-icons/bi';
import { LiaSearchSolid } from 'react-icons/lia';
import { StatusType } from 'pages/Cases/components/Status';
import { PriorityType } from 'shared/typings/priority.types';
import { DepartmentType } from 'shared/typings/department.types';
import { EditButton } from 'shared/components/ActionsButtons/EditButton';
import { DeleteButton } from 'shared/components/ActionsButtons/DeleteButton';
import { FormsQRCode } from './FormsQRCode';
import { TCustomField } from './FormsConfigForm';
import useFormsTable from './hooks/useFormsTable';

type Props = {
	match: { path: string; url: string };
};

export type TFormItem = {
	id: number;
	form_meta_info: {
		company: number;
		department: DepartmentType;
		sub_department: any;
		status: StatusType;
		priority: PriorityType;
		uuid: string;
		id: number;
	};
	fields: TCustomField[];
	title: string;
};
export default function FormsConfigTable({ match }: Props) {
	const { t } = useTranslation();
	const { companyId } = useAppState();
	const {
		onAddFormButtonClicked,
		onCopyToClipBoardHandler,
		onEditFormBtnClicked,
		onDeleteBtnClicked,
		onQRBtnClicked,
		openQRModal,
		deleteFormHandler,
		deleteLoading,
		deleteVisible,
		closeQrModal,
		closeDeletePopup,
		page,
		handlePage
	} = useFormsTable({ match });

	return companyId ? (
		<EntityContainer.All
			entity="AllForms"
			name={`All-feedback-configuration-${companyId}`}
			url={`/${companyId}/feedback_link/`}
			params={{ page, limit: 10 }}
			replaceIds
		>
			{({ isFetched, items, meta }) => {
				return (
					<div>
						{isFetched && !items.length ? (
							<section className="add-form-button mt-16 flex h-full items-center justify-center text-center">
								<div className="add-form-button__wrapper flex flex-col items-center">
									<div className="add-form-button__search rounded-full bg-kgrey-light-grey-4 p-1.5">
										<LiaSearchSolid size={48} color="bg-kgrey-light-grey-1" />
									</div>
									<h3 className="text-xl">{t('you_dont_have_created_forms_yet')}</h3>
									<KntButton
										onClick={onAddFormButtonClicked}
										iconComponent={<BiPlus />}
										color="primary"
										className="mt-3 py-1.5"
									>
										{t('add_form')}
									</KntButton>
								</div>
							</section>
						) : (
							<>
								<div className="flex justify-end">
									<KntButton
										onClick={onAddFormButtonClicked}
										iconComponent={<BiPlus />}
										color="primary"
										className="mt-3 py-1.5"
									>
										{t('add_form')}
									</KntButton>
								</div>
								<TableContainer classes={{ root: 'relative' }}>
									<StyledTable
										aria-label="simple table"
										classes={{ root: 'table-fixed dark-mode-styled-table' }}
									>
										<TableHead>
											<StyledTableRow>
												<StyledTableHeadCell style={{ width: '40%' }}>
													{t('title')}
												</StyledTableHeadCell>
												{/* <StyledTableHeadCell style={{ width: '20%' }}>
													{t('priority')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '20%' }}>
													{t('department')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '20%' }}>
													{t('sub_department')}
												</StyledTableHeadCell> */}
												<StyledTableHeadCell style={{ width: '45%' }}>
													{t('generated_link')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '15%' }}>
													{t('actions')}
												</StyledTableHeadCell>
											</StyledTableRow>
										</TableHead>
										<TableBody>
											{(items as TFormItem[]).map((form) => {
												const formDetail = form?.form_meta_info;
												const computedFeedbackLink = formDetail?.uuid
													? `${window.location.origin}/feedback/${formDetail?.uuid}`
													: null;
												return (
													<StyledTableRow key={form.id}>
														<StyledTableCell>
															<div className="flex items-center">{form?.title}</div>
														</StyledTableCell>
														{/* <StyledTableCell>
															<div className="flex items-center">{priority?.name}</div>
														</StyledTableCell>
														<StyledTableCell>
															<div className="flex items-center">
																{formDetail?.department?.name}
															</div>
														</StyledTableCell>
														{formDetail?.sub_department && (
															<StyledTableCell>
																<div className="flex items-center">
																	{formDetail?.sub_department?.name}
																</div>
															</StyledTableCell>
														)} */}

														<StyledTableCell>
															<CopyToClipboard
																text={computedFeedbackLink ?? ''}
																onCopy={onCopyToClipBoardHandler}
															>
																<div className="flex cursor-pointer items-center break-all hover:text-blue-400">
																	{computedFeedbackLink}
																</div>
															</CopyToClipboard>
														</StyledTableCell>
														<StyledTableCell>
															<div className="flex items-center justify-center gap-x-1 text-center">
																<EditButton clickHandler={onEditFormBtnClicked(form?.id)} />
																<IconBtn
																	classnames="mr-0"
																	onClick={onQRBtnClicked(computedFeedbackLink as string)}
																>
																	<AiOutlineQrcode size={22} />
																</IconBtn>
																<DeleteButton
																	clickHandler={onDeleteBtnClicked({
																		title: form?.title,
																		id: form?.id
																	})}
																/>
																<CopyToClipboard
																	text={computedFeedbackLink ?? ''}
																	onCopy={onCopyToClipBoardHandler}
																>
																	<IconBtn>
																		<AiFillCopy size={22} />
																	</IconBtn>
																</CopyToClipboard>
															</div>
														</StyledTableCell>
													</StyledTableRow>
												);
											})}
										</TableBody>
									</StyledTable>
								</TableContainer>
								<ConfirmModal
									featureHandler={deleteFormHandler(deleteVisible!)}
									isOpen={Boolean(deleteVisible)}
									toggleHandler={closeDeletePopup}
									title={
										<Trans
											i18nKey="branch_delete_confirmation"
											values={{ branchName: `"${deleteVisible?.title}"` }}
										/>
									}
									hasLoader
									isLoading={deleteLoading}
								/>
								<WhiteModal handleModalOpen={closeQrModal} open={Boolean(openQRModal)}>
									<FormsQRCode link={openQRModal?.link as string} />
								</WhiteModal>
								<div className="news__pagination my-4 flex items-center justify-between">
									<Pagination
										count={meta?.last_page}
										page={page ? +page : 1}
										defaultPage={6}
										siblingCount={1}
										color={'primary'}
										onChange={handlePage}
									/>
								</div>
							</>
						)}
					</div>
				);
			}}
		</EntityContainer.All>
	) : (
		<div className="flex justify-center">
			<Loader />
		</div>
	);
}
