import { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router';
import { useEntityActionPopup, useNotistack, usePagination } from 'shared/hooks';
import { useAppState } from 'shared/state';
import actions from 'store/actions';

const formRequest = actions.entity.formRequest;

export default function useFormsTable({ match }: { match: { path: string; url: string } }) {
	const { showNotification } = useNotistack();
	const history = useHistory();
	const dispatch = useDispatch();
	const { t } = useTranslation();
	const { companyId } = useAppState();
	const { page, handlePage, handlePageChange } = usePagination();

	const {
		visible: deleteVisible,
		showVisible: showDeleteVisible,
		loading: deleteLoading,
		toggleLoading: toggleDeleteLoading,
		closePopup: closeDeletePopup
	} = useEntityActionPopup<{ title: string; id: number }>();

	const {
		visible: openQRModal,
		showVisible: showQRModal,
		closePopup: closeQrModal
	} = useEntityActionPopup<{ link: string }>();

	const onCopyToClipBoardHandler = useCallback(() => {
		showNotification({
			message: t('feedback_link_copied')
		});
	}, []);

	const onDeleteBtnClicked = useCallback(
		({ title, id }) =>
			() => {
				showDeleteVisible({ title, id });
			},
		[]
	);

	const onQRBtnClicked = useCallback(
		(link: string) => () => {
			showQRModal({ link });
		},
		[]
	);

	const deleteFormHandler = (formEntity: { id: number }) => () => {
		toggleDeleteLoading();
		dispatch(
			formRequest({
				entity: 'AllForms',
				name: `All-feedback-configuration-${companyId}`,
				method: 'delete',
				url: `${companyId}/feedback_link/${formEntity?.id}/remove/`,
				deleteData: true,
				id: formEntity?.id,
				cb: {
					success: () => {
						showNotification({ message: t('successfully_deleted'), variant: 'success' });
						//Triggers refetch for the table
						handlePageChange(0);
					},
					error: (err) => {
						showNotification({ message: err?.response?.data?.message, variant: 'error' });
					},
					finally: () => {
						toggleDeleteLoading();
						closeDeletePopup();
					}
				},
				params: {},
				primaryKey: 'id',
				values: {}
			})
		);
	};

	const onAddFormButtonClicked = useCallback(() => {
		history.push(`${match.url}/config/create`);
	}, [match]);

	const onEditFormBtnClicked = useCallback(
		(id: number) => () => {
			history.push(`${match.url}/config/edit?formId=${id}`);
		},
		[match]
	);

	return {
		onEditFormBtnClicked,
		onAddFormButtonClicked,
		deleteFormHandler,
		onQRBtnClicked,
		onDeleteBtnClicked,
		onCopyToClipBoardHandler,
		openQRModal,
		closeQrModal,
		deleteVisible,
		deleteLoading,
		closeDeletePopup,
		page,
		handlePage
	};
}
